* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 24px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  margin-top: 75px;
}

#headFont {
  font-size: 72px;
  height: 100px;

  margin-bottom: 70px;
}

.clearLinkStyle {
  color: black;
  text-decoration: none;
}

#main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  width: 60%;
  height: 50px;
}

#newFontButton {
  width: 120px;
  height: 100%;

  font-size: 18px;
  padding: 5px 10px;
  border: 1px solid black;
  border-radius: 18px;
}

#newFontButton:hover {
  cursor: pointer;

  border: 2px solid black;
}

#newFontButton:active {
  border: 3px solid black;
}

#textInput {
  width: 90%;
  padding: 15px;

  font-size: 22px;

  border-radius: 25px;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

@media only screen and (min-width: 1050px) and (max-width: 1550px) {
  #main {
    width: 70%;
  }

  #headFont {
    font-size: 48px;
    height: 90px;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width: 1049px) {
  #wrapper {
    margin-top: 30px;
  }

  #main {
    width: 80%;
    height: 40px;

    gap: 20px;
  }

  #headFont {
    font-size: 28px;
    height: 60px;
    margin-bottom: 15px;
  }

  #textInput {
    min-width: 200px;
    padding: 10px;
    font-size: 12px;
  }

  #main > button {
    font-size: 12px;
    padding: 0 5px;
  }
}
